#skills > ul {
  background-image: linear-gradient(rgba(58, 53, 53, 0.7), var(--bgc)),
    url("../../images/honeycomb-g01a0a9876_640.png");
  margin: auto;

  padding: 0.5rem;
  text-align: center;
  background-size: 100% 100%;
  /* background-color: var(--bgc); */
  width: 20em;
  align-items: center;
  justify-content: center;
  /* margin-left: 40%; */
  border-radius: 10px;
}
#skills > h1 {
  text-align: center;
  margin-bottom: 0.5rem;
}
li {
  display: flex;
  flex-wrap: wrap;
  padding-right: 0.5em;
  align-items: center;
  margin-left: 1em;
}
li > p {
  padding-left: 1rem;
}
@media (max-width: 800px) {
  ul {
    margin: 2em;
    padding: 0;
    align-items: center;
    justify-content: center;
  }
  #skills > li {
    justify-content: start;
  }
}
