#img_1 {
  height: 30em;
  width: 15em;
  border-top-right-radius: 5em;
  background-image: linear-gradient(rgba(58, 53, 53, 0.7), var(--bgc));
  padding: 1em;
  margin: 9em 3em 3em;
}
#home {
  display: flex;
}

#info {
  border-left: 2px solid black;
  font-family: Poppins, sans-serif;
  padding: 2em;
  display: flex;
  flex-direction: column;
  margin: 5em 3em 2em;
  justify-content: center;
}

button {
  padding: 4px;
  border: white;
  margin-right: 0.25em;
  margin-top: 9em;
  background-color: var(--bgc);
  border-radius: 0.25em;
}
button:hover {
  outline-offset: 0em;
  outline: 2px solid rgba(0, 0, 0, 0.3);
}
@media (max-width: 1000px) {
  #home > button {
    margin-left: 0.65em;
    width: 95vw;
  }
  img {
    width: 1rem;
    /* margin: 4rem 3rem 2rem; */
  }
}
