#nav {
  transition-duration: 500ms;
  width: 100vw;
  align-content: center;
  justify-content: space-between;
  position: sticky;
  display: flex;
  background-image: linear-gradient(var(--bgc), rgba(255, 255, 255, 0.94)),
    url("../../images/diamond-g591ae8fd0_640.png");
  /* height: 10rem; */
  /* background-image: linear-gradient(var(--bgc), white 100%),
    url("../../images/honeycomb-g01a0a9876_640.png"); */
}
li:hover {
  opacity: 0.6;
}
a {
  width: fit-content;
}
.sm {
  display: flex;
  justify-content: space-between;
}
.menu {
  display: none;
}
.sm > h2 {
  margin-top: 1em;
  margin-left: 2.5rem;
  background: linear-gradient(rgb(0, 0, 0, 0.7), rgb(7, 79, 98), rgb(8, 53, 8));
  color: transparent;
  background-clip: texts;
  -webkit-background-clip: text;
}
.button {
  display: none;
}
@media (max-width: 800px) {
  .button {
    display: block;
    margin: 0;
    padding: 3px 10px;
    background-color: var(--bgc);
    width: fit-content;
    border-radius: 10em;
    outline: 1px solid rgba(255, 255, 255, 0.268);
  }
  .button::hover {
    opacity: 0.2;
  }
  .button.active {
    transform: rotate(90deg);
  }
  #img_1 {
    height: 30em;
    width: 15em;
    border-top-right-radius: 5rem;
    background-color: var(--bgc);
    padding: 1.5em;
    margin: 9em 7em 2em;
  }
  #home {
    display: flex;
    flex-direction: column;
  }
  #list {
    flex-direction: column;
  }
  li {
    justify-content: center;
  }
  /* #list.active {
    display: flex;
    flex-direction: column;
  } */

  .menu {
    display: block;
    margin: 2em;
  }
  .nav-sm {
    width: 20px;
    display: none;
    /* padding-top: 1.5em;
    width: fit-content;
    padding-right: 2em;
    display: flex; */
  }
  #icon {
    display: none;
    width: 100vw;
  }
  /* #icon {
    background-color: var(--bgc);
    margin: 0.5px;
    margin-left: 90vw;
    margin-top: 5px;
    border-radius: 10px;
    width: 2rem;
    height: 4px;
  } */
  button {
    width: 10px;
  }
  #nav {
    flex-direction: column;
  }
  a {
    text-align: center;
  }
}
.nav-sm {
  display: flex;
  flex-direction: column;
  position: absolute;
}
#list > li > a:hover {
  outline: 3px solid rgba(0, 0, 0, 0.703);
  outline-offset: 0.5em;
  border-radius: 1em;
}
#list > li > a:active {
  outline: 3px solid black;
  outline-offset: 0.5em;
  border-radius: 1em;
}
#list > li {
  background-color: gr;
  background-color: var(--bgc);
  border-radius: 5em;
  padding: 0.5em;

  /* margin-top: 1.5em;
  margin-right: 3em; */
}
li > a {
  text-decoration: none;
  color: #000;
}

#nav > li > a:hover {
  outline-offset: 0.5em;
  outline: 3px solid rgba(15, 173, 52, 0.671);
  border-radius: 5em;
}
#list {
  margin: 1.5em;
  display: flex;
  gap: 1em;
}
a {
  text-decoration: none;
}
