.project {
  padding: 0.5em;
  margin: 2em;
  background-image: linear-gradient(rgba(58, 53, 53, 0.7), var(--bgc));

  /* border: 0.5em double black; */
  border-radius: 1em;
  width: 30em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.project > img {
  width: 20em;
  margin: 0.75em;
}
.project > img:hover {
  opacity: 0.5;
}
#projects > h1 {
  margin-top: 1em;
}
.project > h2,
h4 {
  padding: 3px 17px;
  border-radius: 1em;
  background-color: rgba(0, 0, 0, 0.453);
  background-image: url("../../images/honeycomb-g01a0a9876_640.png");
  background-size: cover;
  color: white;
}
@media (max-width: 1000px) {
  .projList {
    flex-direction: column;
    align-items: center;

    /* margin-left: 20vw; */
  }
  .project {
    width: 80vw;
  }
  .project > img {
    width: 15em;
  }
}
.projList {
  display: flex;
  flex-wrap: wrap;
  padding-right: 0.5em;
  align-items: center;
  justify-content: center;
}
#projects > h1 {
  text-align: center;
}
