* {
  color: rgba(0, 0, 0, 0.8);
  box-sizing: border-box;
  behavior: smooth;
  margin: 0%;
  padding: 0%;
  list-style: none;
  font-family: "Audiowide", sans-serif;

  transition-duration: 500ms;
  animation: bounce 0.5s cubic-bezier(0.5, 0.05, 1, 0.5);
}
