.contact {
  text-align: center;
  margin-top: 0.5rem;
}
.icons {
  margin-top: 0.25rem;
  display: flex;
  gap: 2rem;
  align-items: center;
  justify-content: center;
}
.contact-info {
  margin: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}
.contact-info > input {
  width: 15rem;
  border-radius: 0.25rem;
}
.message {
  height: 10rem;
  text-align: start;
}
.icons {
  background-image: linear-gradient(rgba(58, 53, 53, 0.7), var(--bgc));
  padding: 1rem;
  align-items: center;
  justify-content: center;
}
